.table{
    margin-top: 20px;
    height: 400px;
    width:300px;
    overflow: scroll;
    overflow-x: hidden;
    color: #6a5d5d;
  }
  .table tr:nth-of-type(odd) {
    background-color: #f3f2f8;
  }
  .table td {
    padding: 0.5rem;
  }
  .table tr{
      display:flex;
      justify-content: space-between;
  }